@import url(https://fonts.googleapis.com/css2?family=Antonio:wght@400;700&family=Spartan:wght@400;700&display=swap);

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  background: url(/planets-fact/static/media/background-stars.f6233a6a.svg), rgb(7, 7, 36);
