.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid #fff;
  padding: 16px 24px;
}
.nav {
  display: none;
}
.header .logo {
  color: #fff;
  font-size: 28px;
  letter-spacing: -1.05px;
  text-transform: uppercase;
}

@media (min-width: 768px) {
 
  .header {
    flex-direction: column;
  }
  .header .hamburger-menu {
    display: none;
  }
  .header .nav {
    display: block;
    margin-top: 39px;
    
  }
  .header .nav ul {
    display: flex;
    list-style: none;
  }
  .header ul li {
    margin-left: 33px;
  }
  
  .header a {
    padding: 16px 0;
    text-decoration: none;
    color: #fff;
    font-weight: bold;
    font-size: 11px;
    text-transform: uppercase;
    letter-spacing: 1px;
    transition: all 0.3s ease-in-out;
  }
  .header a.active,
  .header a:hover {
    
    border-top: 3px solid #1eC2A4;
   
    font-weight: bold;
  }
}
@media (min-width: 1024px) {
  .header {
    flex-direction: row;
  }
  .header .nav {
    margin-top: 0;
  }
}

